import { HOST } from 'js/config';
import axios from 'axios';

function getSong(id) {
  var url = HOST + "/song/detail?ids=".concat(id);
  return axios.get(url);
}

function getLyric(id) {
  var url = HOST + "/lyric?id=".concat(id);
  return axios.get(url);
}

function getSimiSongs(id) {
  var url = HOST + "/simi/song?id=".concat(id);
  return axios.get(url);
}

function getSimiPlaylist(id) {
  var url = HOST + "/simi/playlist?id=".concat(id);
  return axios.get(url);
}

function getSongUrl(id) {
  var url = HOST + "/song/url?id=".concat(id);
  return axios.get(url);
}

export { getSong, getLyric, getSimiSongs, getSimiPlaylist, getSongUrl };