import axios from 'axios';
import { HOST } from 'js/config'; // 可选参数：limit： 取出评论数量 , 默认为 20  歌单评论

function getComments(id) {
  var url = HOST + "/comment/playlist?id=".concat(id, "&limit=15");
  return axios.get(url);
}

function getUserPlaylist(id) {
  var url = HOST + "/user/playlist?uid=".concat(id);
  return axios.get(url);
}

function getUser(id) {
  var url = HOST + "/user/detail?uid=".concat(id);
  return axios.get(url);
} // 获取单曲的评论 （取十条


function getSongComments(id) {
  var url = HOST + "/comment/music?id=".concat(id, "&limit=10");
  return axios.get(url);
} // 获取专辑的评论


function getAlbumComments(id) {
  var url = HOST + "/comment/album?id=".concat(id, "&limit=15");
  return axios.get(url);
} // 获取MV的评论


function getMvComments(id) {
  var url = HOST + "/comment/mv?id=".concat(id);
}

export { getComments, getUserPlaylist, getUser, getSongComments, getAlbumComments, getMvComments };